th: 0;\r\n\theight: 0;\r\n\tmargin-left: -5px;\r\n\tvertical-align: middle;\r\n\tcontent: \" \";\r\n\tborder-right: 5px solid transparent;\r\n\tborder-bottom: 5px solid #A10000;\r\n\tborder-left: 5px solid transparent;\r\n}\r\n\r\n\r\n/*********************address********************/\r\n\r\n.address {\r\n\theight: 40px;\r\n\tline-height: 40px;\r\n\tborder-bottom: 1px solid #DBDBDB;\r\n}\r\n\r\n.address a,\r\n.address span {\r\n\tfloat: left;\r\n\tcolor: #777;\r\n}\r\n\r\n.address span {\r\n\tmargin: 0 20px;\r\n}\r\n\r\n.address a.on {\r\n\tcolor: #262626;\r\n}\r\n\r\n\r\n/*********************footer********************/\r\n\r\n.footer {\r\n\tmin-width: 1160px;\r\n}\r\n\r\n.footer .top {\r\n\tpadding: 30px 0;\r\n\tborder-top: 1px solid #DBDBDB;\r\n\tborder-bottom: 1px solid #DBDBDB;\r\n}\r\n\r\n.footer .top .wrapper {\r\n\tdisplay: flex;\r\n}\r\n\r\n.footer .top .wrapper div {\r\n\tflex: 1 1 25%;\r\n\tborder-right: 1px solid #dbdbdb;\r\n}\r\n\r\n.footer .top .wrapper div:last-child {\r\n\tborder-right: none;\r\n}\r\n\r\n.footer .top .wrapper div a img {\r\n\tdisplay: block;\r\n}\r\n\r\n.footer .top .wrapper div a {\r\n\tmargin-left: 25%;\r\n}\r\n\r\n.footer .top .wrapper div span {\r\n\tmargin-top: 15px;\r\n\tmargin-left: 10px;\r\n\tdisplay: block;\r\n\tfont-size: 16px;\r\n}\r\n\r\n.footer p.dibu {\r\n\tbackground: #000;\r\n\tpadding: 30px 0;\r\n\ttext-align: center;\r\n\tcolor: #fff;\r\n}\r\n\r\n\r\n/*********************gotop********************/\r\n\r\nbody {\r\n\tposition: relative;\r\n}\r\n\r\n.gotop {\r\n\tposition: fixed;\r\n\tright: 20px;\r\n\ttop: 400px;\r\n\tz-index: 20;\r\n}\r\n\r\n.gotop a {\r\n\tdisplay: block;\r\n\twidth: 48px;\r\n\theight: 48px;\r\n\tline-height: 38px;\r\n\tbackground: #f4f4f4;\r\n\tmargin-bottom: 10px;\r\n\tborder: 1px solid #d6d6d6;\r\n\tcursor: pointer;\r\n}\r\n\r\n.gotop dl dt {\r\n\tmargin: 0 auto;\r\n\twidth: 20px;\r\n\tpadding-top: 10px;\r\n}\r\n\r\n.gotop dl.goCart {\r\n\tposition: relative;\r\n}\r\n\r\n.gotop dl.goCart span {